// import XLSX from "xlsx";
import * as XLSX from "xlsx";

/**
 * @description:
 * @param {Object} json ÊúçÂä°Á´ØÂèëËøáÊù•ÁöÑÊï∞ÊçÆ
 * @param {String} name ÂØºÂá∫ExcelÊñá‰ª∂ÂêçÂ≠ó
 * @return:
 */
function exportExcel(json, name, header) {
  /* convert state to workbook */
  var data = new Array();
  var keyArray = new Array();

  for (const key1 in json) {
    if (json.hasOwnProperty(key1)) {
      const element = json[key1];
      var rowDataArray = new Array();
      for (const key2 in element) {
        if (element.hasOwnProperty(key2)) {
          const element2 = element[key2];
          rowDataArray.push(element2);
          if (keyArray.length < getLength(element)) {
            keyArray.push(key2);
          }
        }
      }
      data.push(rowDataArray);
    }
  }
  if (header) {
    keyArray = header;
  }
  data.splice(0, 0, keyArray);
  console.log("üöÄ ~ file: excel.js:34 ~ exportExcel ~ keyArray:", keyArray);
  console.log("üöÄ ~ file: excel.js:34 ~ exportExcel ~ data:", data);
  const ws = XLSX.utils.aoa_to_sheet(data);
  // ËÆæÁΩÆÂàóÂÆΩÔºåËøôÈáåËÆæÁΩÆ‰∏∫‰ªéÁ¨¨1ÂàóÂà∞Á¨¨4ÂàóÔºåÂÆΩÂ∫¶ÂàÜÂà´‰∏∫10, 20, 30, 40
  // Ê≥®ÊÑèÔºöÂàóÁöÑÁ¥¢ÂºïÊòØ‰ªé0ÂºÄÂßãÁöÑ
  ws["!cols"] = [
    { wch: 17 }, // Column A
    { wch: 20 }, // Column B
    { wch: 20 }, // Column C
    { wch: 10 }, // Column D
    { wch: 10 }, // Column E
    { wch: 18 }, // Column F
  ];

  // ËÆæÁΩÆË°åÈ´ò‰∏∫20
  ws["!rows"] = Array.from({ length: data.length }, () => ({ hpt: 20 }));

  // ËÆæÁΩÆAÂà∞FÂàóÁöÑÂØπÈΩêÊñπÂºè‰∏∫Ê∞¥Âπ≥ÂûÇÁõ¥Â±Ö‰∏≠
  for (let col = 0; col < 6; col++) {
    ws[col + "1"] = { alignment: { horizontal: "center", vertical: "center" } };
  }

  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, "SheetJS");
  /* generate file and send to client */
  XLSX.writeFile(wb, name + ".xlsx");
}

/**
 * @description: ÂØºÂÖ•excelÊñá‰ª∂Âπ∂ËøîÂõûÊï∞ÊçÆ
 * @param {function} ÂõûË∞ÉÂáΩÊï∞ÂèÇÊï∞data,dataRef,‰∏Ä‰∏™ÊòØÊï∞ÊçÆÔºå‰∏Ä‰∏™ÊòØexceË°®ÂçïÁöÑÁ¥¢Âºï
 * @return:
 */
function importExcel(callback) {
  var inputObj = document.createElement("input");
  inputObj.setAttribute("id", "file");
  inputObj.setAttribute("type", "file");
  inputObj.setAttribute("name", "file");
  inputObj.setAttribute("style", "visibility:hidden");
  inputObj.setAttribute("accept", ".xlsx,.xls,.csv");
  inputObj.addEventListener("change", (evt) => {
    const files = evt.target.files;
    if (files && files[0])
      _file(files[0], (data, dataRef) => {
        callback(data, dataRef);
      });
  });
  document.body.appendChild(inputObj);
  inputObj.value;
  inputObj.click();
}

/**
 * @description: Â§ÑÁêÜÊñá‰ª∂
 * @param {Object} file Êñá‰ª∂ÂØπË±°
 * @param {function} callback ÂõûË∞ÉÂáΩÊï∞
 * @return:
 */
function _file(file, callback) {
  const make_cols = (refstr) =>
    Array(XLSX.utils.decode_range(refstr).e.c + 1)
      .fill(0)
      .map((x, i) => ({
        name: XLSX.utils.encode_col(i),
        key: i,
      }));

  /* Boilerplate to set up FileReader */
  const reader = new FileReader();
  reader.onload = (e) => {
    /* Parse data */
    const bstr = e.target.result;
    const wb = XLSX.read(bstr, {
      type: "binary",
    });
    /* Get first worksheet */
    const wsname = wb.SheetNames[0];
    const ws = wb.Sheets[wsname];
    /* Convert array of arrays */
    const data = XLSX.utils.sheet_to_json(ws, {
      header: 1,
    });
    /* Update state */
    callback(data, make_cols(ws["!ref"]));
  };
  reader.readAsBinaryString(file);
}

/**
 * @description: Ëé∑ÂèñmapÁöÑÈïøÂ∫¶
 * @param {Object} obj mapÂØπË±°
 * @return: mapÁöÑÈïøÂ∫¶
 */
function getLength(obj) {
  var count = 0;
  for (var i in obj) {
    if (obj.hasOwnProperty(i)) {
      count++;
    }
  }

  return count;
}
export default {
  exportExcel,
  importExcel,
};
